<template>
    <div>
      <button @click="getNum(100)">机选</button>
      <ul class="list">
        <li v-for="(item, index) in list" :key="index">
          <button class="red" v-for="red in item.red" :key="red">{{red}}</button>
          <button class="blue">{{item.blue}}</button>
        </li>
      </ul>
    </div>
</template>
<script>


export default ({
    data() {
      return {
        blueBall: '',
        redBalls: [],
        list: [],
        allReds: []
      }
    },
    mounted(){
      for(let i = 0; i < 33; i++) {
        this.allReds.push(i + 1)
      }
    },
    methods: {
      getBalls(){
        this.redBalls = [];
        this.blueBall = '';
        for (let i = 0; i < 33; i++) {
          let num = Math.ceil(Math.random() * 33)
          if (!this.redBalls.includes(num) && this.redBalls.length < 6) {
            this.redBalls[i] = num
          }
        }
        this.blueBall = Math.ceil(Math.random() * 15)
        let item = {red: this.redBalls, blue: this.blueBall}
        this.list.push(item)
      },
      getNum(num) {
        for(let i = 0; i<= num; i++) {
           this.getBalls()
        }
      }
    }
})
</script>
<style scoped>
.list{
  list-style: none;
  display: flex;
  flex-wrap: wrap;
}
.list li{
  display: flex;
  width: 25%;
  padding: 10px 0;
}
.list li button{
  width: 30px;
  height: 30px;
 
  border-radius: 100%;
  margin-left: 10px;
  color: #fff;
}
.red{
  background:red;
}
.blue{
  background: blue;
}
</style>
