import Vue from 'vue'
import VueRouter from 'vue-router'
import mapDraw from '../views/mapDraw.vue'
import test from '../views/test.vue'
import mapIndex from '../views/mapIndex.vue'
import doubleColorBall from "../views/doubleColorBall.vue"

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'openlayersTutorial',
    component:  () => import("@/views/openlayer-tutorial/index.vue")
  },
  {
    path: '/mapDraw',
    name: 'mapDraw',
    component: mapDraw
  },

  {
    path: '/test',
    name: 'test',
    component: test
  },
  {
    path: '/mapIndex',
    name: 'mapIndex',
    component: mapIndex
  },
  {
    path: '/doubleColorBall',
    name: 'doubleColorBall',
    component: doubleColorBall
  }
]

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes
})

export default router
