<template>
  <div id="map"></div>
</template>
<script>
import "ol/ol.css";
import "ol-ext/dist/ol-ext.min.css";
import Map from "ol/Map";
import View from "ol/View";
import TileLayer from "ol/layer/Tile";
import OSM from "ol/source/OSM";
import VectorSource from "ol/source/Vector";
import VectorLayer from "ol/layer/Vector";
import Feature from "ol/Feature";
import { LineString, Polygon, Point } from "ol/geom";
import { Style, Icon } from "ol/style";

import Transform from "ol-ext/interaction/Transform";

let imgSrc = require("../assets/img.svg")
export default {
  data () {
    return {
      map: "",
      basicLayer: null,
      basicSource: null,
      imageLayer: null,
      imageSource: null,
      oxtTransform: "",
      currentFeature: null,
    };
  },
  methods: {
    initMap () {
      let that = this;
      that.map = new Map({
        view: new View({
          center: [106.51, 36.55],
          zoom: 5,
          projection: "EPSG:4326",
        }),
        layers: [
          new TileLayer({
            source: new OSM(),
          }),
        ],
        target: "map",
      });
    },
    addBasicLayer () {
      let that = this;
      that.basicSource = new VectorSource();
      that.basicLayer = new VectorLayer({
        name: "Vecteur",
        source: that.basicSource,
      });
      that.map.addLayer(that.basicLayer);
    },
    addImageLayer () {
      let that = this;
      that.imageSource = new VectorSource();
      that.imageLayer = new VectorLayer({
        name: "Vecteur",
        source: that.imageSource,
      });
      that.map.addLayer(that.imageLayer);
    },
    addTestFeatures () {
      let that = this;
      that.basicSource.addFeature(
        new Feature(
          new LineString([
            [110, 36],
            [108, 26],
            [88, 40],
          ])
        )
      );
      that.basicSource.addFeature(
        new Feature(
          new Polygon([
            [
              [111, 37],
              [109, 27],
              [89, 41],
            ],
          ])
        )
      );
      let imgPoint = new Feature(new Point([106.51, 36.55]));
      imgPoint.image_type = "Image";
      that.imageSource.addFeature(imgPoint);
      let imgStyle = new Style({
        image: new Icon({
          src: imgSrc,
          scale: 0.1
        }),
      });
      imgPoint.setStyle(imgStyle);
    },
  },
  mounted () {
    let that = this;
    that.initMap();
    that.addBasicLayer();
    that.addImageLayer();
    that.addTestFeatures();
    that.oxtTransform = new Transform({
      layers: [that.imageLayer],
      enableRotatedTransform: true,
      hitTolerance: 2,
      translateFeature: true,
      scale: true,
      rotate: true,
      keepAspectRatio: false,
      keepRectangle: true,
      translate: true,
      stretch: true,
      pointRadius: function (f) {
        if (f.image_type == "Image") {
          let currentStyle = typeof f.getStyle() == "object" ? f.getStyle() : f.getStyle()();
          let img = currentStyle.getImage().getImage();
          let currentScale = currentStyle.getImage().scaleArray_;
          let radius = f.get("radius") || [img.width * currentScale[0] / 2, img.height * currentScale[1] / 2];
          f.set("radius", radius);
          return radius;
        }
      },
    });

    var startangle = 0;
    var startRadius = 10;
    var d = [0, 0];

    // Handle rotate on first point
    var firstPoint = false;
    that.oxtTransform.on(["select"], function (e) {
      if (e.feature) {
        that.currentFeature = e.feature;
        let currentStyle = typeof that.currentFeature.getStyle() == "object" ? that.currentFeature.getStyle() : that.currentFeature.getStyle()();
        let currentIcon = currentStyle.getImage();
        that.imgRotate = that.imgRotateIng = (((currentIcon.getRotation() * 180) / Math.PI) % 360).toFixed(2);
      } else {
        if (that.currentFeature != null) {
          console.log("失去选中");
        }
      }

      if (firstPoint && e.features && e.features.getLength()) {
        that.oxtTransform.setCenter(
          e.features.getArray()[0].getGeometry().getFirstCoordinate()
        );
      }
    });

    that.oxtTransform.on(["rotatestart", "translatestart", "scalestart"], function (e) {
      startangle = e.feature.get("angle") || 0;
      startRadius = e.feature.get("radius") || 10;
      d = [0, 0];
    });

    that.oxtTransform.on("rotating", function (e) {
      that.imgRotate = Number((((((startangle - e.angle) * 180) / Math.PI - 180) % 360) + 180).toFixed(2));
      e.feature.set("angle", startangle - e.angle);
      let currentStyle = typeof e.feature.getStyle() == "object" ? e.feature.getStyle() : e.feature.getStyle()();
      let img = currentStyle.getImage().getImage();
      console.log(img.width,img.height)
      // let scale = currentStyle.getImage().getScale();
      let rotate = startangle - e.angle;
      console.log(rotate * 180 / Math.PI)
      currentStyle.getImage().setRotation(rotate)
      // e.feature.setStyle(new Style({
      //   image: new Icon({
      //     src: img,
      //     scale: scale,
      //     rotation: rotate,
      //   }),
      // }));
    });

    that.oxtTransform.on("translating", function (e) {
      d[0] += e.delta[0];
      d[1] += e.delta[1];
      if (firstPoint) {
        that.oxtTransform.setCenter(
          e.features.getArray()[0].getGeometry().getFirstCoordinate()
        );
      }
    });

    that.oxtTransform.on("scaling", function (e) {
      // $('#info').text("scale: "+e.scale[0].toFixed(2)+","+e.scale[1].toFixed(2));
      if (firstPoint) {
        that.oxtTransform.setCenter(
          e.features.getArray()[0].getGeometry().getFirstCoordinate()
        );
      }
      let radiusX = startRadius[0] * Math.abs(e.scale[0]), radiusY = startRadius[1] * Math.abs(e.scale[1]);
      radiusX = radiusX == 0 ? 0.05 : radiusX;
      radiusY = radiusY == 0 ? 0.05 : radiusY;
      if (e.features.getLength() === 1) {
        var feature = e.features.item(0);
        feature.set("radius", [radiusX, radiusY]);
      }
      let currentStyle = typeof e.feature.getStyle() == "object" ? e.feature.getStyle() : e.feature.getStyle()();
      let wh = currentStyle.getImage().getImage();
      let scale = [(radiusX * 2) / wh.width, (radiusY * 2) / wh.height];
      let rotate = currentStyle.getImage().getRotation();
      let img = currentStyle.getImage().getSrc();
      e.feature.setStyle(
        new Style({
          image: new Icon({
            src: img,
            scale: scale,
            rotation: rotate,
          }),
        })
      );
    });
    that.oxtTransform.on(
      ["rotateend", "translateend", "scaleend"],
      function (e) {
        console.log("结束", e);
      }
    );

    that.map.addInteraction(that.oxtTransform);

    // 点击地图获取经纬度，feature
    that.map.on("click", function (e) {
      that.Latitudelongitude = e.coordinate;
      let feature = that.map.forEachFeatureAtPixel(e.pixel, function (feature) {
        return feature;
      });
      console.log(feature, e.coordinate);
    });
  },
};
</script>
>
<style scoped>
#map {
  width: 100%;
  height: 100%;
}
</style>